export interface OPacksGridInit extends OGridSmallInit {
	/** Actions associées aux items sélectionnés (menu contextuel et IFocusActionables). */
	actions?: IAction<IPackActionCtx>[]

	/** Racourcis claviers à capter sur la sel courante. */
	accelKeyMgr?: AccelKeyMgr<IPackActionCtx>

	/** Supprime le lineDrawer par défaut (avec null) ou customise le lineDrawer. */
	lineDrawer?: null | IGridLineDrawer

}

export interface OPacksMgrInit extends OSkinableInit {
	reg?: IReg<IUniverseEnv>
	universes: (CoreUniverse & IPacksInUniverse)[];
	/** Init de la liste des résultats. */
	packsGrid?: OPacksGridInit
	/** extensions autorisées pour l'installation (avec le .)
	 * La première valeur sera utilisée comme extension de téléchargement **/
	packExtensions?: string[];
}

interface PacksMgr<PACKOBJ extends JPackBase, PACKOBJGLOBAL extends IPacksGlobal & JPackBase> extends BaseElementAsync, IView, IGridLineDrawer, IPackActionCtx, IInstallPackActionCtx {
	initialize(init: OPacksMgrInit): this
}

abstract class PacksMgr<PACKOBJ extends JPackBase, PACKOBJGLOBAL extends IPacksGlobal & JPackBase> extends BaseElementAsync implements IGridLineDrawer, IPackActionCtx, IInstallPackActionCtx {

	reg: IReg<IUniverseEnv>;

	packSrvList: PackSrv[] = [];

	packsToolbar?: BarActions<IPackActionCtx> | null;

	grid: GridSmall & IContextMenuActionsPointer<IPackActionCtx>;

	fetchState: 'pending' | 'done' | 'failed';

	extension4download: string = ".pack";

	/** Packs en cours d'installation **/
	protected _installPendingPacks: Map<String, PACKOBJGLOBAL> = new Map<String, PACKOBJGLOBAL>();

	/** Dernière liste de packs retournée aprés interrogation des différents portals **/
	protected _lastPacksListFromSrvs: (PACKOBJGLOBAL)[];

	/** Liste de tous les packs installés dans cette instance packMgr (même aprés refresh) **/
	protected fullSessionInstallPacksIds: ({ packSrv: PackSrv, pack?: PACKOBJ })[] = [];

	protected async _initialize(init: OPacksMgrInit): Promise<void> {
		// Reg dédié car des actions par d&faut sont ajoutées au reg courant, et cet objet peut etre multi instancié dans un meme contexe reg
		this.reg = REG.createSubReg(this.findReg(init));

		if (init.universes)
			init.universes.forEach((univ) => {
				if (univ.packServer) this.packSrvList.push(univ.packServer);
			})

		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this.reg.installSkin("webzone:panel", sr);
		this._initAndInstallSkin(this.localName, init);

		if (init.packExtensions && init.packExtensions.length > 0) {
			this.extension4download = init.packExtensions[0];
			this.packExtensions = init.packExtensions;
		}

		/** Init les listes du registre **/
		this.overrideInitialize(init);
		this.reg.addToList("actions:packs:packsmgr:toolbar", "actionRefreshUi", 1, actionRefreshUi);

		/** Accelkey **/
		let accelKeyMgr = new AccelKeyMgr().initFromMapActions(this.reg.mergeListsAsMap("accelkeys:packs:packsmgr"));

		/** Toolbar **/
		let packsActions = ACTION.injectSepByGroup(this.reg.mergeLists("actions:packs:packsmgr:toolbar", "actions:packs:packsmgr") as IAction<IPackActionCtx>[], this.reg.getPref("actions.packs.packsmgr.groupOrder", "packs pack * refresh"), this);
		accelKeyMgr.initFromMapActions(this.reg.getListAsMap("accelkeys:wspApp:global"));

		this.packsToolbar = sr.appendChild(<BarActions class="horizontal" î={{
			reg: this.reg,
			actions: packsActions,
			actionContext: this,
			uiContext: "dialog",
			disableFullOverlay: true
		} as OBarActionsInit<IPackActionCtx>}/> as BarActions<IPackActionCtx>);

		/** Grid **/
		this.grid = new GridSmall();

		const initPacksGrid: OPacksGridInit = BASIS.newInit(init.packsGrid, this.reg);
		if (initPacksGrid.skinScroll === undefined) initPacksGrid.skinScroll = 'scroll/small';
		if (initPacksGrid.lineDrawer === undefined) initPacksGrid.lineDrawer = this;
		if (initPacksGrid.accelKeyMgr === undefined) initPacksGrid.accelKeyMgr = accelKeyMgr;
		initPacksGrid.dataHolder = new GridDataHolderJsonTree<IPortalPacks<PACKOBJGLOBAL> | PACKOBJGLOBAL>('packs').setOpenAll(true);
		initPacksGrid.selType = "multi";
		initPacksGrid.emptyBody = this.emptyBody.bind(this);
		initPacksGrid.skinOver = "packsmgr-grid";
		initPacksGrid.autoSelOnFocus = 'first';

		this.grid.ctxMenuActions = {
			actions: ACTION.injectSepByGroup(this.reg.mergeLists("actions:packs:packsmgr:tree", "actions:packs:packsmgr") as IAction<IPackActionCtx>[], this.reg.getPref("actions.packs.packsmgr.groupOrder", "packs pack * refresh"), this) as IAction<IPackActionCtx>[],
			actionContext: this,
		};

		this.grid.addEventListener('keydown', (ev: KeyboardEvent) => {
			accelKeyMgr.handleKeyboardEvent(ev, this);
		});

		this.grid.addEventListener('grid-select', function (this: GridSmall, ev: Event) {
			const packsMgr = DOMSH.findHost<PacksMgr<any, any>>(this);
			packsMgr.packsToolbar.refreshContent();
		});

		this.grid.initialize(initPacksGrid);
		this.grid.setAttribute("c-resizable", "");
		sr.appendChild(this.grid);

		return this.refreshUi();
	}

	/**
	 * Ajoute une couche de configuration dans le init courant
	 */
	protected overrideInitialize(init: OPacksMgrInit): void {}

	/**
	 * Refresh UI
	 * Gestion de la réentrance
	 */
	async refreshUi(scope: "local" | "full" = "full") {
		try {
			if (this._refreshPending) {
				this._newRefreshRequired = (this._newRefreshRequired == "full" ? "full" : scope);
				return;
			}
			this._refreshPending = true;
			await this._refreshUi(scope);
		} finally {
			this._refreshPending = false;
			if (this._newRefreshRequired) {
				let state = this._newRefreshRequired;
				this._newRefreshRequired = null;
				if (state == "full" && this._fullAutoRefrehPlanned)
					clearTimeout(this._fullAutoRefrehPlanned);
				setTimeout(this.refreshUi.bind(this, state), 500);
			}
		}
	}

	protected _newRefreshRequired: "local" | "full" | null;
	protected _refreshPending: boolean = false;
	protected _fullAutoRefrehPlanned: number;
	protected _showLastNotInstalledPacksOnRefreshAll: boolean = false;

	private async _refreshUi(scope: "local" | "full" = "full") {
		try {
			this.fetchState = "pending";
			let packsListByPortal: Map<PackSrv, PACKOBJGLOBAL[]> = new Map<PackSrv, PACKOBJGLOBAL[]>();

			if (this._installPendingPacks)
				this._installPendingPacks.forEach(pack => {
					if (!packsListByPortal.has(pack.packSrv))
						packsListByPortal.set(pack.packSrv, []);
					packsListByPortal.get(pack.packSrv).push(pack);
				});

			if (!this._lastPacksListFromSrvs || scope == "full")
				await this.loadPacksListFromSRvs();

			this._lastPacksListFromSrvs.forEach(pack => {
				if (!packsListByPortal.has(pack.packSrv))
					packsListByPortal.set(pack.packSrv, []);
				packsListByPortal.get(pack.packSrv).push(pack);

				if (!this.isPackStatusPending(pack.installStatus))
					pack.title = this.buildPackTitle(pack as IPacksBaseGlobal);

				// Affectation du titre si vide en exploitant les infos des derniers packs installés
				// Pertinent dans le cas des packs en cours d'installation
				if (!pack.title) {
					let installPackProps = this.fullSessionInstallPacksIds.find(value => {
						return value.pack.id === pack.id && value.packSrv === pack.packSrv;
					});
					if (installPackProps && installPackProps.pack) pack.title = installPackProps.pack.title;
				}
				// Si le pack est dans un statut "pending", on programme un refresh
				if (this.isPackStatusPending(pack.installStatus)) {
					if (this._fullAutoRefrehPlanned)
						clearTimeout(this._fullAutoRefrehPlanned);
					this._fullAutoRefrehPlanned = setTimeout(() => this.refreshUi("full"), 2000);
				}
			});

			if (this._showLastNotInstalledPacksOnRefreshAll) {
				this.fullSessionInstallPacksIds?.forEach((entry) => {
					if (entry.pack && !this.isPackStatusInstalled(entry.pack.installStatus)) {
						if (!packsListByPortal.has(entry.packSrv))
							packsListByPortal.set(entry.packSrv, []);
						packsListByPortal.get(entry.packSrv).push(Object.assign({}, entry.pack, {packSrv: entry.packSrv}) as any);
					}
				});
			}

			let finalPacksList: (IPortalPacks<PACKOBJGLOBAL> | PACKOBJGLOBAL)[] = [];
			if (this.packSrvList.length == 1) {
				// Pas de matérialisation du niveau "portal"
				packsListByPortal.forEach((packs, packSrv) => {
					finalPacksList = packs;
				});
			} else {
				packsListByPortal.forEach((packs, packSrv) => {
					finalPacksList.push({
						title: packSrv.universe.getName(),
						packSrv,
						packs,
					});
				});
			}

			this.fetchState = "done";
			(this.grid.dataHolder as GridDataHolderJsonArray<IPortalPacks<PACKOBJGLOBAL> | PACKOBJGLOBAL>).setDatas(finalPacksList);
		} catch (e) {
			this.fetchState = "failed";
			(this.grid.dataHolder as GridDataHolderJsonArray<IPortalPacks<PACKOBJGLOBAL> | PACKOBJGLOBAL>).setDatas([]);
			throw e;
		}
	}

	/**
	 *
	 */
	protected buildPackTitle(pack: IPacksBaseGlobal): string {
		return WSPPACK.buildPackTitle(pack);
	}

	/**
	 * Suivi de l'installation du pack
	 * @param file
	 * @param packSrv
	 * @param pack
	 */
	async packInstallHandler(state: "start" | "end", file: File, packSrv: PackSrv, pack?: PACKOBJ): Promise<void> {
		const installuid = file.name + "_" + file.size + "_" + packSrv.universe.getId();
		if (state == "start") {
			if (!this._installPendingPacks.get(installuid)) {
				this._installPendingPacks.set(installuid, {
					id: installuid,
					packSrv: packSrv,
					title: file.name,
					installStatus: "uploadPending"
				} as PACKOBJGLOBAL);
				await this.refreshUi("local");
			}
		} else
			this._installPendingPacks.delete(installuid);

		if (pack) {
			if (!pack.title) pack.title = file.name;
			this.fullSessionInstallPacksIds.push({packSrv, pack});
		}
	}

	async globalInstallHandler(state: "start" | "end", packsAreInstalled: boolean): Promise<void> {
		if (state == "end" && packsAreInstalled)
			await this.refreshUi("full");
	}

	/**
	 * Tree : affichage explicite des lignes en erreur
	 * @param row
	 * @param line
	 */
	redrawLine(row: IGridDataRow, line: HTMLElement): void {
		const packSrv = row.getData("packSrv") as PackSrv;
		// Statut d'installation
		const installStatus = row.getData("installStatus") as EWspPackStatus;
		const error = row.getData("error") as EWspPackErrors;
		if (installStatus == "installFailed" || error)
			DOM.addClass(line, "error");
		else if (installStatus == "uploadPending" || installStatus == "installPending")
			DOM.addClass(line, "pending");
		else {
			DOM.removeClass(line, "error");
			DOM.removeClass(line, "pending");
		}
		//
		if ((packSrv && !packSrv.universe.reg.hasPerm("action.packsMgr#uninstall.wspPack")) || row.getData("system"))
			DOM.addClass(line, "readonly");
		else
			DOM.removeClass(line, "readonly");

		// Nouvelles installations
		const packId = row.getData("id") as string;
		if (packSrv && packId && this.isNewPack(packSrv, packId)) {
			DOM.addClass(line, "new");
			setTimeout(() => {DOM.removeClass(line, "new")}, 5000);
		} else
			DOM.removeClass(line, "new");

	}

	/**
	 * Retourne true si le pack a été installé dans l'instance packMgr courante
	 * @param packSrv
	 * @param packId
	 */
	protected isNewPack(packSrv: PackSrv, packId: string): boolean {
		return (packId && packSrv && this.fullSessionInstallPacksIds.findIndex(value => {
			return value.pack.id === packId && value.packSrv === packSrv;
		}) > -1);
	}

	/**
	 * Recharge la liste des packs affichés des serveurs
	 */
	abstract loadPacksListFromSRvs(): Promise<void[]>

	/**
	 * Retourne true si le statut du pack est de type "pending"
	 */
	abstract isPackStatusPending(status: any): boolean

	/**
	 * Retourne true si le statut du pack est "installé""
	 */
	abstract isPackStatusInstalled(status: any): boolean

	/**
	 * Tree : fonction retournant le texte à afficher en cas de tree vide
	 */
	protected emptyBody() {
		switch (this.fetchState) {
		case "done":
			return <c-msg>Aucun pack installé</c-msg>;
		case "failed":
			return <c-msg level="error">Échec au chargement</c-msg>;
		default:
			return <c-msg>Chargement en cours...</c-msg>;
		}
	}

}

