/** Struct de donnée globale aux n packsMgr du portal */
interface IPacksGlobal {
	packSrv: PackSrv,
}


interface IPortalPacks<E extends IPacksGlobal> {
	// nom du portal
	title: string,
	packSrv: PackSrv,
	packs: E[],
}

interface IPacksBaseGlobal extends IPacksGlobal, JWspPackBase {}

interface IPacksInstalledGlobal extends IPacksGlobal, JWspPackInstalled {}


// ###
// # WspPack
// #

export class WspPacksMgr extends PacksMgr<JWspPackInstalled | JWspPackBase, IPacksBaseGlobal | IPacksInstalledGlobal> {

	protected async _initialize(init: OPacksMgrInit): Promise<void> {
		if (!init.packExtensions) init.packExtensions = [".wsppack"];
		super._initialize(init);
	}

	protected overrideInitialize(init: OPacksMgrInit): void {
		/** Actions **/
		this.reg.addToList("actions:packs:packsmgr", "actionRemovePacks", 1, RemoveWspPackAction.SINGLETON);
		this.reg.addToList("actions:packs:packsmgr", "actionDownloadPack", 1, DownloadWspPackAction.SINGLETON);

		this.reg.addToList("actions:packs:packsmgr:toolbar", "actionInstallPack", 1, InstallWspPackAction.SINGLETON);

		/** Accelkeys **/
		this.reg.addToList("accelkeys:packs:packsmgr", "Delete", 1, RemoveWspPackAction.SINGLETON);

		/** Grid **/
		let columnDefs = [];
		columnDefs.push(
			new GridColTreeDef('packTitle').setLabel("Pack")
				.setDefaultSort(1, 'ascendant')
				.setFlex('25em', 1, 1).setMinWidth('8em').setSortable(true)
				.setCellBuilder(new CellBuildeWspPackTitle()),
			new GridColDef('installDate').setLabel("Date").setDescription("Date d'installation")
				.setFlex('3em', 1, 1).setMinWidth('3em').setSortable(true)
				.setCellBuilder(new CellBuilderDate('installDate').setCellClass("center")
					.setDescriptionFunc(function (this: CellBuilderDate, row: IGridDataRow) {
						const d = this.getDate(row);
						return d ? Date.prototype.toLocaleDateString.call(this.getDate(row), "fr", {weekday: "long", year: "numeric", month: "long", day: "numeric", hour: "numeric", minute: "numeric", second: "numeric"}) : "";
					})),
			new GridColDef('installStatus').setLabel("Statut")
				.setFlex('5em', 1, 1).setMinWidth('4em').setSortable(true)
				.setCellBuilder(new CellBuilderEnum('installStatus', new Map([["uploadPending", "Envoi en cours..."], ["installPending", "Installation en cours..."], ["installed", "Installé"], ["installFailed", "Non installé (échec)"]])).setCellClass("center")
					.override("_getValue", function (this: CellBuilderEnum, row: IGridDataRow): any {
						if (row.getData("packs")) {
							return "";
						} else {
							if (row.getData("system"))
								return "Système (non modifiable)"
							else {
								if (WSPPACK.isStatusInstalled(row.getData("installStatus")) && row.getData("error")) return "Non disponible";
								return this.mappingTable.get(row.getData("installStatus")) || "Disponible";
							}
						}
					})
					.setDescriptionFunc((row)=>WSPPACK.errorToString(row.getData("error") as EWspPackErrors))
				)
			,
		);


		if (!init.packsGrid) init.packsGrid = [] as OPacksGridInit;
		if (init.packsGrid.columnDefs === undefined) init.packsGrid.columnDefs = columnDefs;
	}

	isPackStatusPending(status: any): boolean {
		return WSPPACK.isStatusPending(status);
	}

	isPackStatusInstalled(status: any): boolean {
		return WSPPACK.isStatusInstalled(status);
	}

	async loadPacksListFromSRvs() {
		this._lastPacksListFromSrvs = [];
		return Promise.all(this.packSrvList.map(async (packSrv) => {
			if (packSrv.universe.reg.hasPerm("action.wspPacksMgr#list")) {
				try {
					let packs = await WSPPACK.listPacks(packSrv);
					if (packs)
						await Promise.all(packs.map((pack) => {
							let config: (IPacksBaseGlobal | IPacksInstalledGlobal) = Object.create(pack);
							config.packSrv = packSrv;
							this._lastPacksListFromSrvs.push(config);
						}));
				} catch (e) {
					POPUP.showNotifError("Rafraichissement partiel de l'écran", this.grid);
					await ERROR.log(e);
				}
			}
		}));
	}

	/**
	 * Retourne true si les packs peuvent être affichés sur au moins un packSrv
	 */
	static isAvailable(universes: (CoreUniverse & IPacksInUniverse)[]): boolean {
		if (universes) {
			return universes.findIndex((univ) => {
				return univ.reg.hasPerm("action.wspPacksMgr#list");
			}) > -1;
		}
		return false;
	}
}

customElements.define("wsp-packsmgr", WspPacksMgr);

