/**
 * Colonne de titre du wsp pack
 */
class CellBuildedSkinPackTitle extends CellBuildeWspPackTitle {

	protected _getIcon(row: IGridDataRow): string {
		const packSrv = row.getData("packSrv") as PackSrv;
		const skinSetCode = row.getData("skinSetCode");
		const hasSkinSetIcon = row.getData("skinSetIcon") ? true : false;
		const installStatus = row.getData("installStatus") as ESkinPackStatus;
		if (this._isPortalRow(row))
			return null;
		return ((!SKINPACK.isStatusPending(installStatus) && packSrv && skinSetCode && hasSkinSetIcon) ? SKINPACK.getIconUrl(packSrv, skinSetCode) : "") || "/@skin@/wsp/dialogs/packsMgr/skinppack.svg";
	}

	protected _getDescription(row: IGridDataRow): any {
		let fullDescStr: string[] = [];
		if (row.getData("error"))
			fullDescStr.push(SKINPACK.errorToString(row.getData("error") as ESkinPackErrors));
		// - buildId
		const buildId = row.getData("buildId");
		if (buildId)
			fullDescStr.push(`Identifiant de compilation du pack : ${buildId}`);
		// - skinClasses
		const skinClasses: string[] = row.getData("skinClasses");
		if (skinClasses && skinClasses.length) {
			fullDescStr.push(`Classes de skin stylées par cet habillage graphique :`);
			skinClasses.forEach((entry) => fullDescStr.push(` - ${entry}`))
		}
		return fullDescStr.join("\n");
	}
}

/** Cellule donnant les modèles installés compatibles avec le skinPack */
export class CellBuilderSkinPackModels extends CellBuilderString {
	constructor(protected packsMgr: PacksMgr<any, any>) {
		super(null);
	}

	redrawCell(row: GridDataRowJson<IPacksBaseGlobal | IPacksInstalledGlobal>, root: HTMLElement): void {
		const cacheHolder = row.cacheHolder;
		let cache: JSkinClassesForWspType[] | Promise<JSkinClassesForWspType[]> = cacheHolder["-models"];
		const rowDatas = row.rowDatas;

		if (cache == null) {
			DOM.setTextContent(root, '...');
			cacheHolder["-models"] = cache = SKINPACK.ListSkinClassesForWspType(rowDatas.packSrv, rowDatas.id).then((resp) => {
				if (cacheHolder["-models"] !== cache) return; //raceCond : les données de cette row ont évoluées.
				return cacheHolder["-models"] = resp;
			});
		}

		if (cache instanceof Promise) {
			//cache non reseté mais calcul en cours, on attend
			DOM.setTextContent(root, '...');
			(root as any)._modelsPending = cache; //raceCond pour refresh async de la line HTML.
			cache.then(() => {
				if ((root as any)._modelsPending !== cache) return; //raceCond : cette line HTML a été redessinée
				(root as any)._modelsPending = null; //cleanup
				redrawCellUi.call(this);
			});
		} else {
			(root as any)._modelsPending = null; //raceCond pour refresh async de la line HTML.
			redrawCellUi.call(this);
		}

		function redrawCellUi(this: CellBuilderSkinPackModels) {
			DOM.setTextContent(root, null);
			const skinClassesForWspType: JSkinClassesForWspType[] = cacheHolder["-models"];
			if (skinClassesForWspType && skinClassesForWspType.length) {
				const list = skinClassesForWspType.sort((e1, e2) => {
					if ((e1.wspTypeRef as JWspOptionDef).isOption && !(e2.wspTypeRef as JWspOptionDef).isOption) return 1;
					if ((e2.wspTypeRef as JWspOptionDef).isOption && !(e1.wspTypeRef as JWspOptionDef).isOption) return -11;
					return e2.wspTypeRef.title.localeCompare(e1.wspTypeRef.title);
				});
				if (skinClassesForWspType.length < 3) {
					DOM.setTextContent(root, skinClassesForWspType.map((entry) => entry.wspTypeRef.title).join(", "));
				} else {
					const base = skinClassesForWspType.slice(0, 2).map((entry) => entry.wspTypeRef.title).join(", ");
					DOM.setTextContent(root, `${base}...`);
				}
				root.title = "Modèles installés utilisant cet habillage graphique :\n";
				root.title += skinClassesForWspType.map((entry) => `- ${entry.wspTypeRef.title}`).join("\n");
			} else {
				DOM.setTextContent(root, 'Aucun');
				delete root.title;
			}
			//DOM.setAttr(refElt, "href", cacheHolder["-models"]);
			//DOM.setTextContent(refElt, cacheHolder["-models"]);
		}

	}
}

